import { remove, uniqueId, find } from 'lodash'
import Vue from 'vue'

/**
 *  zIndex 
 */
class Node {
  /**
   * 
   *
   * @public
   * @type Node
   */
  parent = null

  /**
   *  priority 
   * [
   *   {
   *     priority: 1,
   *     children: [node1, node2, ...]
   *   },
   *   {
   *     priority: 2,
   *     children: [node3, node4, ...]
   *   }
   * ]
   *
   * @private
   * @type Array
   */
  childrenGroup = []

  /**
   * 
   *
   * @public
   * @readonly
   * @type string
   */
  id = null

  /**
   * zIndex 
   *
   * @public
   * @type number
   */
  zIndex = null

  constructor () {
    this.id = uniqueId('overlay-node-id-')
  }

  /**
   * 
   *
   * @public
   * @param {Function} callback 
   */
  iterateChildren (callback) {
    for (let i = 0, il = this.childrenGroup.length; i < il; i++) {
      const group = this.childrenGroup[i]
      for (let j = 0, jl = group.children.length; j < jl; j++) {
        const child = group.children[j]
        if (callback(child, j, group)) {
          return child
        }
      }
    }
  }

  /**
   *  priority 
   *
   * @public
   * @param {Node} child 
   * @param {number} priority  children group 
   */
  appendChild (child, priority = 1) {
    let index = null
    let isInserted = false
    find(this.childrenGroup, (group, i) => {
      if (group.priority === priority) {
        group.children.push(child)
        isInserted = true
        return true
      }

      if (group.priority > priority) {
        index = i
        return true
      }
    })

    if (!isInserted) {
      if (index === null) {
        this.childrenGroup.push({
          children: [child],
          priority
        })
      } else {
        this.childrenGroup.splice(index, 0, {
          children: [child],
          priority
        })
      }
    }

    child.parent = this
  }

  /**
   *  id 
   *
   * @private
   * @param {string} id  id
   */
  removeChildById (id) {
    for (let i = 0, il = this.childrenGroup.length; i < il; i++) {
      const group = this.childrenGroup[i]
      remove(group.children, child => {
        if (child.id === id) {
          child.parent = null
          return true
        }
      })
    }
  }

  /**
   * 
   *
   * @public
   */
  remove () {
    if (!this.parent) {
      return
    }

    this.parent.removeChildById(this.id)
  }

  /**
   * 
   *
   * @public
   */
  getChildrenCount () {
    let counter = 0
    this.childrenGroup.forEach((group) => {
      counter += group.children.length
    })
    return counter
  }
}

/**
 *  zIndex 
 */
export class Tree {
  /**
   * 
   *
   * rootNode: {
   *   parent: null,
   *   childrenGroup: [
   *     {
   *        priority: 1,
   *        children: [...]
   *     }
   *   ],
   *   id: 'overlay-node-id-1',
   *   zIndex: 200
   * }
   *
   * @type Node
   */
  rootNode = new Node()

  /**
   *  id  id 
   *
   * @private
   * @type Object
   */
  nodeMap = {
    [this.rootNode.id]: {
      node: this.rootNode,
      instance: null
    }
  }

  /**
   *  zIndex
   *
   * @private
   * @type number
   */
  baseZIndex = 100

  /**
   *  zIndex
   *
   * @public
   * @param {number} zIndex  zIndex
   */
  setBaseZIndex (zIndex) {
    this.baseZIndex = zIndex
  }

  /**
   * 
   *
   * @private
   * @param {Node} curNode 
   * @param {Function} callback 
   */
  iterate ({ curNode = this.rootNode, callback } = {}) {
    const result = callback(curNode)
    if (result) {
      return result
    }

    return curNode.iterateChildren(child => this.iterate({ curNode: child, callback }))
  }

  /**
   * 
   *
   * @private
   * @param {string} parentId  id
   * @param {Node} node 
   * @param {number} priority 
   */
  insertNode (parentId, node, priority) {
    const parent = this.nodeMap[parentId]
    if (!parent) {
      return
    }

    const parentNode = parent.node
    parentNode.appendChild(node, priority)

    this.generateTreeZIndex()
  }

  /**
   * 
   *
   * @public
   * @param {string} parentId  id
   * @param {number} priority 
   */
  createNode ({ parentId = this.rootNode.id, priority } = {}) {
    const node = new Node()

    const nodeId = node.id
    const instance = new Vue({
      data () {
        return {
          id: nodeId
        }
      },
      methods: {
        remove: () => {
          node.remove()
          this.nodeMap[nodeId] = null
        },
        appendTo: (parentId, priority) => {
          this.nodeMap[node.id] = { node, instance }
          this.moveNode(node, parentId, priority)
        },
        toTop: () => {
          let targetGroup
          let targetIndex
          node.parent.iterateChildren((child, index, group) => {
            if (child === node) {
              targetGroup = group
              targetIndex = index
              return true
            }
          })

          const children = targetGroup.children
          children.push(...children.splice(targetIndex, 1))
          this.generateTreeZIndex()
        }
      }
    })
    this.nodeMap[nodeId] = { node, instance }
    this.insertNode(parentId, node, priority)
    Vue.nextTick(() => {
      instance.$emit('zindexchange', node.zIndex)
    })

    return instance
  }

  /**
   *  ``  ``  children 
   *
   * @private
   * @param {Node} node 
   * @param {string} parentId  id
   * @param {number} priority 
   */
  moveNode (node, parentId, priority) {
    const realParentId = parentId || this.rootNode.id
    const parentNode = this.nodeMap[realParentId].node
    const curParentNode = node.parent
    if (parentNode === curParentNode) {
      return
    }

    node.remove()
    this.nodeMap[realParentId].node.appendChild(node, priority)

    this.generateTreeZIndex()
  }

  /**
   *  zIndex 
   *  zIndex 
   *
   * @private
   */
  generateTreeZIndex () {
    let baseZIndex = this.baseZIndex

    this.iterate({
      callback: child => {
        if (child === this.rootNode) {
          return
        }

        const instance = this.nodeMap[child.id].instance
        let zIndex = baseZIndex++
        let isEqual = child.zIndex === zIndex
        child.zIndex = zIndex

        if (!isEqual) {
          instance.$emit('zindexchange', child.zIndex)
        }
      }
    })
  }
}

export default new Tree()



